html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: rgba(0, 0, 0, 0);
}

abbr[title] {
  border-bottom: none;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: .35em .75em .625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  max-width: 100%;
  white-space: normal;
  padding: 0;
  display: table;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template, [hidden] {
  display: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --primary-bg-color: #e8e8e2;
  --title-black-text-color: #010101;
  --secondary-text-color: rgba(1, 1, 1, .3);
  --title-white-text-color: #e8e8e2;
  --primary-bg-green-color: #586f1f;
  --secondary-bg-green-color: #6d8434;
  --card-bg-color: #fafafa;
  --cancel-background: rgba(1, 1, 1, .07);
  --backdrop-background: rgba(1, 1, 1, .4);
  --modal-text: rgba(1, 1, 1, .7);
  --pagination-color: #a3a3a3;
  --white-color: #fff;
  --span-info-color: rgba(1, 1, 1, .3);
  --hero-color: rgba(1, 1, 1, .8);
  --footer-color: rgba(232, 232, 226, .6);
  --footer-second-color: rgba(232, 232, 226, .7);
  --footer-bg-color: rgba(232, 232, 226, .2);
  --footer-border-color: rgba(232, 232, 226, .3);
  --filter-color: rgba(1, 1, 1, .5);
  --body-cart-color: rgba(109, 132, 52, .05);
}

*, :before, :after {
  box-sizing: border-box;
}

@font-face {
  font-family: Neue Montreal Medium;
  src: url("PPNeueMontreal-Medium.0183d55c.otf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Neue Montreal Regular;
  src: url("PPNeueMontreal-Book.a5f3aed6.otf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Quincy CF;
  src: url("QuincyCF-Black.4fe7690f.ttf");
  font-weight: normal;
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Regular;
  font-size: 12px;
  font-weight: 400;
}

input {
  width: 100%;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1440px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media only screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

body {
  background: var(--primary-bg-color);
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

main {
  flex: 1;
}

footer {
  flex-shrink: 0;
}

.cart-products {
  max-height: 540px;
  margin-bottom: 80px;
  padding-right: 40px;
  overflow-x: hidden;
}

.section-cart {
  padding-top: 37px;
  padding-bottom: 80px;
}

.cart-title {
  color: var(--title-black-text-color);
  text-transform: uppercase;
  align-items: center;
  margin-bottom: 14px;
  font-family: Neue Montreal Medium;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
  display: flex;
}

.cart-icon-shopping {
  background: var(--secondary-bg-green-color);
  fill: var(--primary-bg-color);
  width: 38px;
  height: 38px;
  border-radius: 50%;
  margin-right: 14px;
  padding: 10px;
}

.delete-all-box {
  cursor: pointer;
  justify-content: end;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
  padding-right: 7px;
  display: flex;
}

.delete-all-boxt:hover {
  fill: var(--secondary-text-color);
  color: var(--secondary-text-color);
}

.delete-all-text {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.delete-all-icon {
  fill: var(--title-black-text-color);
}

.cart-content, .cart-shopping-list {
  flex-direction: column;
  display: flex;
}

.cart-shopping-item {
  padding: 30px 0;
  display: flex;
}

.cart-shopping-item:not(:last-child) {
  border-bottom: 1px solid var(--secondary-text-color);
}

.cart-shopping-img {
  width: 100px;
  height: 100px;
  background: var(--card-bg-color);
  border-radius: 10px;
  margin-right: 20px;
}

.cart-item-info {
  width: 100%;
  flex-direction: column;
  display: flex;
}

.cart-info-top {
  justify-content: space-between;
  margin-bottom: 10px;
  display: flex;
  position: relative;
}

.cart-delete-icon {
  width: 18px;
  height: 18px;
  cursor: pointer;
  position: absolute;
  top: -8px;
  right: 0;
}

.cart-info-name-product {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.cart-properties-list {
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 16px;
  display: flex;
}

.cart-properties-item {
  align-items: baseline;
  gap: 4px;
  display: flex;
}

.cart-properties-caption {
  color: var(--secondary-text-color);
  font-family: Neue Montreal Regular;
  font-size: 12px;
  line-height: 1.16;
}

.cart-properties-subtitle {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Regular;
  font-size: 12px;
  line-height: 1.16;
}

.cart-info-bottom {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.cart-info-price {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.cart-counter-wrapper {
  border: 1px solid var(--secondary-text-color);
  width: 74px;
  height: 26px;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  padding: 4px;
  display: flex;
}

.cart-counter-value {
  color: var(--title-black-text-color);
  margin: 0 6px;
  font-family: Neue Montreal Regular;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.12;
}

.cart-counter-decrement, .cart-counter-increment {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: rgba(0, 0, 0, 0);
  font-size: 20px;
  font-weight: 400;
  line-height: 1.12;
}

.cart-title-your-order {
  color: var(--title-black-text-color);
  text-transform: uppercase;
  margin-bottom: 40px;
  font-family: Neue Montreal Medium;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
}

.cart-total-price-box {
  width: 100%;
  background: var(--body-cart-color);
  border-radius: 8px;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 30px;
  padding: 14px 18px;
  display: flex;
}

.cart-total-price-text {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.cart-total-price-number {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
}

.cart-total-price-sum {
  color: var(--secondary-text-color);
  font-family: Neue Montreal Regular;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.cart-form {
  width: 100%;
  position: relative;
}

.cart-form-input {
  border: 1px solid var(--span-info-color);
  color: var(--title-black-text-color);
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 14px;
  padding: 12px 12px 12px 45px;
  font-family: Neue Montreal Regular;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.cart-form-btn {
  width: 100%;
  background: var(--secondary-bg-green-color);
  color: var(--title-white-text-color);
  border: none;
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  padding: 12px;
  font-family: Neue Montreal Medium;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.cart-empty {
  width: 100%;
  flex-direction: column;
  align-items: center;
  margin: 105px 0 120px;
  display: flex;
}

.cart-empty-retina {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cart-empty-img {
  width: 132px;
  height: 114px;
  margin-bottom: 28px;
}

.cart-empty-text {
  color: var(--title-black-text-color);
  margin-bottom: 14px;
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.11;
}

.cart-empty-description {
  width: 90%;
}

.cart-empty-link {
  color: var(--secondary-bg-green-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.11;
  text-decoration: none;
}

.cart-empty-description {
  color: var(--secondary-text-color);
  text-align: center;
  font-family: Neue Montreal Regular;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
}

.filters {
  background-color: var(--primary-bg-color);
  padding-top: 37px;
  padding-bottom: 20px;
}

.filters-title {
  color: var(--span-info-color);
  margin-bottom: 10px;
  font-size: 14px;
}

.filters-form {
  width: 100%;
  flex-direction: column;
  gap: 14px;
  display: flex;
}

.filters-input::-ms-input-placeholder {
  color: #010101;
  font-family: Neue Montreal Regular;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.filters-input::placeholder {
  color: #010101;
  font-family: Neue Montreal Regular;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.filters-input, .filters-categories, .filters-allTypes {
  cursor: pointer;
}

.filters-search-wrap, .filters-wrap {
  position: relative;
}

.filters-input, .filters-categories, .filters-allTypes {
  border: 1px solid var(--span-info-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 30px;
  padding: 14px 20px;
  font-size: 14px;
}

.filters-allTypes {
  background-color: var(--card-bg-color);
  border-color: rgba(0, 0, 0, 0);
}

.filters-input:focus {
  outline: 1px solid var(--secondary-bg-green-color);
}

.filters-search-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-content: center;
  justify-content: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: -2px;
  transform: translate(-50%, -50%);
}

.filters-down-svg {
  fill: var(--primary-bg-color);
  stroke: var(--title-black-text-color);
  transition: all .3s;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
}

.rotate {
  transform: translate(-50%, -50%)rotate(180deg);
}

.filters-categories-list, .filters-allTypes-list {
  z-index: 2;
  width: 100%;
  background: var(--white-color);
  visibility: hidden;
  opacity: 0;
  border-radius: 15px;
  padding: 14px 20px;
  transition: all .3s;
  position: absolute;
  top: 56px;
  left: 0;
  box-shadow: 0 4px 11.2125px rgba(0, 0, 0, .04), 0 20px 69px rgba(0, 0, 0, .07);
}

.filters-allTypes-list {
  z-index: 1;
}

.list-active {
  visibility: visible;
  opacity: 1;
  border: 1px solid var(--span-info-color);
}

.filters-categories-item, .filters-allTypes-item {
  color: var(--filter-color);
  cursor: pointer;
  margin-bottom: 9px;
  font-size: 14px;
}

.filters-categories-item:hover, .filters-allTypes-item:hover {
  color: var(--title-black-text-color);
}

.footer-section {
  background-color: var(--secondary-bg-green-color);
  color: var(--primary-bg-color);
}

.footer-logo-wrapper {
  padding-top: 40px;
  padding-bottom: 20px;
}

.footer-logo-link {
  color: var(--primary-bg-color);
  letter-spacing: -.36px;
  align-items: center;
  gap: 8px;
  font-family: Quincy CF;
  font-size: 18px;
  font-style: normal;
  font-weight: 900;
  line-height: 20px;
  display: flex;
}

.footer-logo-icon {
  width: 28px;
  height: 28px;
  background-color: var(--primary-bg-color);
  fill: var(--secondary-bg-green-color);
  border-radius: 50%;
  flex-shrink: 0;
  padding: 6px;
  transition: all .3s;
}

.footer-logo-icon:hover, .footer-logo-icon:focus {
  fill: var(--primary-bg-color);
  background-color: var(--secondary-bg-green-color);
  border: 1px solid var(--primary-bg-color);
}

.footer-soc-wrapper {
  padding-bottom: 40px;
}

.footer-soc-list {
  gap: 12px;
  display: flex;
}

.footer-soc-svg {
  fill: var(--primary-bg-color);
  border: 1px solid var(--footer-border-color);
  border-radius: 10px;
  padding: 8px;
}

.footer-soc-svg:hover, .footer-soc-svg:focus {
  background: var(--footer-bg-color);
  border-radius: 10px;
}

.footer-title {
  margin-bottom: 14px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
}

.footer-text-wrapper {
  padding-bottom: 80px;
}

.footer-text {
  color: var(--footer-second-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.footer-form-wrapper {
  margin-bottom: 80px;
}

.footer-form-title {
  color: var(--primary-bg-color);
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.footer-form-input {
  border: 1px solid var(--primary-bg-color);
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 30px;
  margin-bottom: 14px;
  padding: 12px 18px;
}

.footer-form-input::-ms-input-placeholder {
  color: var(--footer-color);
  font-family: Neue Montreal Regular;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.29;
}

.footer-form-input::placeholder {
  color: var(--footer-color);
  font-family: Neue Montreal Regular;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.29;
}

.footer-form-btn {
  color: var(--title-black-text-color);
  background-color: var(--primary-bg-color);
  width: 100%;
  border: none;
  border-radius: 30px;
  padding: 12px 149px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
}

.footer-form-btn:hover, .footer-form-btn:focus {
  color: var(--secondary-bg-green-color);
}

.footer-privacy-wrapper {
  padding-bottom: 80px;
}

.footer-subtitle {
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.footer-privacy {
  margin-right: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  transition: all .3s;
  display: inline-block;
}

.footer-privacy:hover, .footer-privacy:focus {
  transform: scale(1.1);
}

.footer-span {
  margin-right: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.footer-terms {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  transition: all .3s;
  display: inline-block;
}

.footer-terms:hover, .footer-terms:focus {
  transform: scale(1.1);
}

.header-page {
  background-color: var(--primary-bg-color);
  padding-top: 18px;
  padding-bottom: 36px;
}

.header-page-container {
  width: 100%;
  padding: 0;
}

.header-page-navigation {
  width: 100%;
}

.header-list {
  width: 100%;
  align-items: center;
  gap: 6px;
  display: flex;
}

.header-first-elements {
  align-items: center;
  margin-right: auto;
  display: flex;
}

.header-logotype {
  color: var(--title-black-text-color);
  letter-spacing: -.36px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin-right: auto;
  font-family: Quincy CF;
  font-size: 18px;
  font-style: normal;
  font-weight: 900;
  line-height: 20px;
  display: flex;
}

.header-home-ref {
  width: 77px;
  height: 38px;
  color: var(--primary-bg-color);
  background-color: var(--secondary-bg-green-color);
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  font-family: Neue Montreal Medium;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.header-home-ref:hover, .header-home-ref:focus {
  background-color: var(--primary-bg-green-color);
}

.header-logotype-icon {
  width: 28px;
  height: 28px;
  background-color: var(--secondary-bg-green-color);
  fill: var(--primary-bg-color);
  border-radius: 50%;
  flex-shrink: 0;
  padding: 6px;
}

.header-logotype-icon:hover, .header-logotype-icon:focus {
  background-color: var(--primary-bg-green-color);
  fill: var(--primary-bg-color);
}

.header-logotype-icon-card {
  width: 38px;
  height: 38px;
  background-color: var(--secondary-bg-green-color);
  fill: var(--primary-bg-color);
  border-radius: 50%;
  flex-shrink: 0;
  padding: 10px;
}

.header-logotype-icon-card:hover, .header-logotype-icon-card:focus {
  background-color: var(--primary-bg-green-color);
}

.header-shopping-card-link {
  justify-content: center;
  align-items: center;
  display: flex;
}

.hidden {
  display: none;
}

img {
  max-width: 100%;
  height: auto;
  border-radius: 60px;
  display: block;
}

#slider {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.slider-container {
  min-height: 480px;
}

.slider-container picture {
  opacity: 0;
  width: 100%;
  z-index: 1;
  transition: all .5s;
  position: absolute;
}

.slider-container picture.active {
